@font-face {
  font-family: "icomoon";
  src: url("../img/icons/fonts/icomoon.eot?mnhis6");
  src: url("../img/icons/fonts/icomoon.eot?mnhis6#iefix") format("embedded-opentype"), url("../img/icons/fonts/icomoon.ttf?mnhis6") format("truetype"), url("../img/icons/fonts/icomoon.woff?mnhis6") format("woff"), url("../img/icons/fonts/icomoon.svg?mnhis6#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-facebook::before {
  content: "\e900";
  color: #03133d;
}
.icon-favourite::before {
  content: "\e901";
  color: #03133d;
}
.icon-instagram::before {
  content: "\e902";
  color: #03133d;
}
.icon-linkedin::before {
  content: "\e903";
  color: #03133d;
}
.icon-star::before {
  content: "\e904";
  color: #cdcdcd;
}
.icon-twitter::before {
  content: "\e905";
  color: #03133d;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
ul,
ol {
  padding: 0;
  margin: 0;
}
body,
h1,
h2,
h3,
h4,
h5,
p,
li,
figure,
input {
  margin: 0;
}
ul {
  list-style: none;
}
a {
  text-decoration: none;
  color: black;
}
a:visited {
  color: inherit;
}
html {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  line-height: 24px;
}
button {
  background-color: inherit;
  font-family: 'Montserrat', sans-serif;
  border: 0;
  cursor: pointer;
}
.wrapper {
  padding: 1.07rem;
}
@media (min-width: 1400px) {
  .wrapper {
    padding: 1.07rem 12.86rem;
  }
}
.burger {
  background: url(../img/burger.svg) no-repeat center center;
}
.btn {
  background-color: #26A9E0;
  color: #ffffff;
  border: 0;
  padding: 1.76rem;
  width: 100%;
  font-weight: 600;
  margin-bottom: 1em;
  text-transform: uppercase;
}
.btn:hover {
  background: linear-gradient(to bottom right, #26A9E0, #2687E0);
}
@media (min-width: 992px) {
  .btn {
    width: 18.85rem;
  }
}
.banner-title {
  font-size: 30px;
  line-height: 33px;
}
@media (min-width: 992px) {
  .banner-title {
    font-size: 70px;
    line-height: 80px;
    font-weight: 900;
  }
}
.section-title {
  font-size: 25px;
  font-weight: 700;
}
@media (min-width: 992px) {
  .section-title {
    font-size: 50px;
    line-height: 60px;
  }
}
.menu {
  display: flex;
  justify-content: center;
  gap: 2em;
  text-transform: uppercase;
}
.menu .current__item {
  position: relative;
}
.menu .current__item::after {
  content: "";
  width: 100%;
  display: block;
  position: absolute;
  height: 4px;
  background-color: #26A9E0;
  bottom: -25px;
}
.menu__item {
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.menu__item:hover {
  color: #26A9E0;
}
.list {
  display: flex;
  justify-self: end;
  gap: 2em;
  align-items: flex-start;
}
.list__item {
  height: 100%;
}
.list__item + .list__item + .list__item {
  display: none;
  position: relative;
}
.list__item + .list__item + .list__item::after {
  content: "3";
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  color: #ffffff;
  position: absolute;
  top: -7px;
  right: -4px;
  width: 1.25rem;
  height: 1.25rem;
  background-color: #26A9E0;
  border-radius: 50%;
}
@media (min-width: 992px) {
  .list__item + .list__item + .list__item {
    display: block;
  }
}
.list__btn {
  height: 100%;
}
.socials {
  display: inline-flex;
  gap: 2em;
}
@media (min-width: 992px) {
  .socials__icon {
    display: block;
  }
}
.socials__icon::before {
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.socials__icon:hover::before {
  color: #26A9E0;
}
.contact {
  font-size: 14px;
}
.contact__text {
  display: inline-block;
  color: #03133D;
  opacity: 50%;
}
.contact__number {
  margin: 0 8em 0 0.5em;
  color: #26A9E0;
  font-weight: 600;
}
.contact__number::after {
  content: "";
  display: inline-block;
  width: 1em;
  height: 1em;
  background: url(../img/phone.svg) no-repeat bottom center;
  margin-left: 0.5em;
}
.contact .language {
  font-size: 14px;
  font-weight: 600;
  position: relative;
}
.contact .language::before {
  content: "";
  display: inline-block;
  width: 3px;
  height: 3px;
  background-color: #26A9E0;
  position: absolute;
  top: 0.64em;
  left: -3.86em;
}
.contact .language::after {
  content: "";
  border: solid black;
  border-width: 0 2.5px 2.5px 0;
  display: inline-block;
  padding: 2.5px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  margin: 0 0 3px 0.5em;
}
.rating__icon:hover::before,
.rating__icon:hover::before ~ .rating__icon::before {
  color: #FFDD15;
}
.rating__icon:hover::before {
  color: #FFDD15;
}
.product {
  position: relative;
}
@media (min-width: 992px) {
  .product {
    font-size: 18.8px;
  }
}
.product--new::before {
  content: "NEW";
  background-color: #26A9E0;
}
.product--top::before {
  content: "TOP";
  background-color: #A7D6CA;
}
.product--discount::before {
  content: "-30%";
  background-color: #F37A7A;
}
.product::before {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 4.38em;
  height: 2.25em;
  text-align: center;
  color: white;
  font-size: 8px;
  font-weight: 600;
}
@media (min-width: 992px) {
  .product::before {
    font-size: 12px;
  }
}
.product__img {
  width: 100%;
  padding: 0.64em;
}
.product__title {
  font-size: 0.85em;
  font-weight: 400;
  padding: 0 2em 1em 2em;
  line-height: 1.5em;
  height: 3.5em;
}
@media (min-width: 992px) {
  .product__title {
    display: block;
  }
}
.product__stars {
  display: inline-flex;
  justify-content: center;
  flex-direction: row-reverse;
}
.product__reviews {
  display: block;
  font-size: 0.7em;
  color: #929298;
  margin: 1em 0.5em 1em;
}
@media (min-width: 992px) {
  .product__reviews {
    display: inline-block;
  }
}
.product__price {
  display: block;
  margin-bottom: 1em;
  font-weight: 600;
  line-height: 12px;
}
.product__btn-buy {
  display: none;
}
.header {
  padding: 1.07rem;
  display: grid;
  grid-template-columns: 2em 1fr 8fr;
  gap: 2em;
  align-items: center;
  border-bottom: 1px solid #DEE7F1;
}
@media (min-width: 1400px) {
  .header {
    padding: 1.07rem 12.86rem;
  }
}
@media (min-width: 992px) {
  .header {
    grid-template-columns: auto 1fr auto;
  }
}
.header__socials {
  display: none;
}
@media (min-width: 992px) {
  .header__socials {
    display: flex;
  }
}
.header__contact {
  display: none;
  grid-column: 2/-1;
  justify-self: end;
}
@media (min-width: 992px) {
  .header__contact {
    display: block;
  }
  .header__contact::before {
    content: "";
    position: absolute;
    top: 3.5em;
    left: 0px;
    border-bottom: 1px solid #DEE7F1;
    width: 100%;
    display: block;
  }
}
.header__burger {
  width: 2em;
  height: 2em;
}
